<template>
  <t-cell
    class="toon-field"
    :title="label"
    v-clickoutside="doCloseActive"
    :class="[{
      'is-textarea': type === 'textarea',
      'is-nolabel': !label
    }]">
    <textarea
      @change="$emit('change', currentValue)"
      ref="textarea"
      class="toon-field-core"
      :placeholder="placeholder"
      v-if="type === 'textarea'"
      :rows="rows"
      :disabled="disabled"
      :readonly="readonly"
      v-model="currentValue">
    </textarea>
    <input
      @change="$emit('change', currentValue)"
      ref="input"
      class="toon-field-core"
      :placeholder="placeholder"
      :number="type === 'number'"
      v-else
      :type="type"
      @focus="active = true"
      :disabled="disabled"
      :readonly="readonly"
      :value="currentValue"
      @input="handleInput">
    <div
      @click="handleClear"
      class="toon-field-clear"
      v-if="!disableClear"
      v-show="currentValue && type !== 'textarea' && active">
      <i class="iconfont toon-field-error"></i>
    </div>
    <span class="toon-field-state" v-if="state" :class="['is-' + state]">
      <i class="iconfont" :class="['toon-field-' + state]"></i>
    </span>
    <div class="toon-field-other">
      <slot></slot>
    </div>
  </t-cell>
</template>

<script>
import TCell from '../Tcell/index.vue';
export default {
  name: 'mt-field',
  data() {
    return {
      active: false,
      currentValue: this.value
    };
  },
  props: {
    type: {
      type: String,
      default: 'text'
    },
    rows: String,
    label: String,
    placeholder: String,
    readonly: Boolean,
    disabled: Boolean,
    disableClear: Boolean,
    state: {
      type: String,
      default: 'default'
    },
    value: {},
    attr: Object
  },
  components: { TCell },
  methods: {
    doCloseActive() {
      this.active = false;
    },
    handleInput(evt) {
      this.currentValue = evt.target.value;
    },
    handleClear() {
      if (this.disabled || this.readonly) return;
      this.currentValue = '';
    }
  },
  watch: {
    value(val) {
      this.currentValue = val;
    },
    currentValue(val) {
      this.$emit('input', val);
    },
    attr: {
      immediate: true,
      handler(attrs) {
        this.$nextTick(() => {
          const target = [this.$refs.input, this.$refs.textarea];
          target.forEach(el => {
            if (!el || !attrs) return;
            Object.keys(attrs).map(name => el.setAttribute(name, attrs[name]));
          });
        });
      }
    }
  }
};
</script>

<style lang="css">
  .toon-field {

      display: -webkit-box;

      display: -ms-flexbox;

      display: flex;
  }

  .toon-field .toon-cell-title {

      width: 80px;

      -webkit-box-flex: 0;

          -ms-flex: none;

              flex: none;
  }

  .toon-field .toon-cell-value {

      -webkit-box-flex: 1;

          -ms-flex: 1;

              flex: 1;

      color: inherit;

      display: -webkit-box;

      display: -ms-flexbox;

      display: flex;
  }

  .toon-field.is-nolabel .toon-cell-title {

      display: none;
  }

  .toon-field.is-textarea {

      -webkit-box-align: inherit;

          -ms-flex-align: inherit;

              align-items: inherit;
  }

  .toon-field.is-textarea .toon-cell-title {

      padding: 10px 0;
  }

  .toon-field.is-textarea .toon-cell-value {

      padding: 5px 0;
  }

  .toon-field-core {

      -webkit-appearance: none;

         -moz-appearance: none;

              appearance: none;

      border-radius: 0;

      border: 0;

      -webkit-box-flex: 1;

          -ms-flex: 1;

              flex: 1;

      outline: 0;

      line-height: 1.6;

      font-size: inherit;

      width: 100%;
  }

  .toon-field-clear {
      opacity: .4;
      .iconfont{
        width:20px;
        height:20px;
        display:inline-block;
      }
      .iconfont-field-error{
        background: url(data:image/png;base64,);
        background-position: no-repeat;
        background-size: 20px;
      }
  }

  .toon-field-state {
      color: inherit;
      margin-left: 20px;
  }

  .toon-field-state.is-default {

      margin-left: 0;
  }

  .toon-field-state.is-success {

      color: #4caf50;
  }

  .toon-field-state.is-warning {

      color: #ffc107;
  }

  .toon-field-state.is-error {

      color: #f44336;
  }

  .toon-field-other {

      top: 0;

      right: 0;

      position: absolute;
  }
  .toon-field-state{
    .iconfont-field-success{
      background: url(data:image/png;base64,);
      background-position: no-repeat;
      background-size: 20px;
      width:20px;
      height:20px;
      display:inline-block;
    }
    .iconfont-field-error{
      background: url(data:image/png;base64,);
      background-position: no-repeat;
      background-size: 20px;
      width:20px;
      height:20px;
      display:inline-block;
    }
    .iconfont-field-warning{
      background: url(data:image/png;base64,);
      background-position: no-repeat;
      background-size: 20px;
      width:20px;
      height:20px;
      display:inline-block;
    }
  }
  .toon-field::before{
    left:90px;
  }
</style>



